.Modal-Popup {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 150;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(32, 32, 32, 0.5);
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.Modal-exit {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 15px;
  right: 15px;
  height: 25px;
  width: 25px;
  font-size: 15px;
  color: #8f8f8f;
  border: 0;
  outline: 0;
  cursor: pointer;
  transition: 0.3s ease;
}

.Modal-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 65vh;
  max-width: 75vw;
}
.Modal-nav {
  height: 200px;
}

.Modal-nav-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.Modal-nav-buttons li {
  display: inline;
  padding: 15px 10px;
  margin: 5px 5px;
  text-align: center;
  border-radius: 10px;
  font-size: 14px;
  color: rgb(30, 30, 30);
  cursor: pointer;
  background-color: #b5dada;
  text-decoration: none;
  transition: 0.3s ease;
}

.Modal-nav-buttons li:hover {
  background-color: #c6eeee;
}

.Modal-Content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 65vh;
  width: 100%;
  background: #f8ffff;
  border-radius: 10px;
}

.Modal-img {
  width: 75%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  height: 100%;
  margin: 0 25px;
}

.Modal-Content .Modal-Content-text {
  display: flex;
  margin: 50px 25px;
  flex-direction: column;
  font-size: 0.8em;
}

.Modal-Content-text p {
  font-family: "Arial";
  font-size: 1.2em;
  margin-top: 15%;
  align-self: center;
}

@media screen and (max-width: 768px) {
  .Modal-wrapper {
    max-width: 100%;
    height: 90vh;
  }
  .Modal-Content {
    height: 100vh;
    width: 100%;
    border-radius: 0;
    flex-direction: column;
  }
  .Modal-exit {
    right: 30px;
  }

  .Modal-img {
    height: 35%;

    margin-left: auto;
    margin-right: auto;
  }
}
